<template>
  <div class="container">
    <div class="header-container">
      <h1 class="header">projects</h1>
      <div class="monitor-container">
        <div class="monitor">
          <div class="chosen-img">
            <img :src="chosenImage" alt="no signal">
          </div>
        </div>
      </div>
    </div>
          <div>
      </div>
    <p>click/tap on a project to see it on the monitor</p>
    <div class="wrapper" >
    <div class="projects-container" @click="toggleGreenBg('project'+i),displayPic(proj.img)"  v-for="(proj,i) of this.projectObjects" :key="'project'+i" :class="'project project'+i" >

      <!-- <div > -->
      <Project  :name="proj.name"
                :link="proj.link"
                :description="proj.description"
                :techs="proj.techs"
                :additionals="proj.additionals"
                :responsive="proj.responsive"
                :img="proj.img"
                :getImage="getImage" 
                />
      <!-- </div> -->
      
    </div>
    </div>


  </div>
</template>

<script>
import Project from './Project.vue'

export default {
  name: 'Projects',
  components: {
    Project
  },
  data(){
    return {
        defaultImage: 'no-signal',
        chosenImage: this.getImage("no-signal"),

        projects: [
            [
              'mine sweeper', 
              'https://itai-rozen.github.io/minesweeper', 
              'mine sweeper game', 
              ['Vanilla Js'],
              ['empty cell expansion using recursive algorythm', 'hints for displaying a patch of cells for brief moment', '3 difficuly levels'],
              'No.',
              'minesweeper',
              
              
            ],
            [
              'Book Shop',
              'https://itai-rozen.github.io/book-shop',
              'A website for a non-existing book shop',
              ['Vanilla Js'],
              ['information is randomly hard-coded', 'all the CRUD options, translation option using data-set tag'],
              'Yes. Landscape & Portrait',
              'bookshop',

            ],
            [
            'Sudoku',
            'https://itai-rozen.github.io/sudoku',
            'Soduko game',
            ['Vanilla Js'],
            ['numbers are randomly set every game', '3 difficulty levels'],
            'Yes. Landscape & Portrait.',
              'sudoku',
            ],
            [
            'Beat Maker',
            'https://itai-beatmaker.herokuapp.com/',
            'create your own beats using a 16-bit looper with various instruments',
            ['React','Express','MongoDB'],
            [
              'save presets you take pride in', 
              'control volume & BPM',
              'useSound hook enables samples to override other samples without giving glitchy sound problem',
              'also the useSound hook helps organizing all the samples in one mp3, making it easier to maintain the app & to update more instruments'],
              'Yes. Landscape & Portrait.',
              'beatmaker'

            ],
            [
            'Hebrew Calendar',
            'https://heb-calendar.herokuapp.com',
            'An app for a Hebrew\Gregorian calendar & organizer',
            ['Vue Cli 2.0','Vue-Router', 'Express','MongoDB', 'heb-cal API'],
            [
              'times of candle-lighting on Shabats & holidays displayed on calendar & on seperate tab',
              'Parashat Shavu\'a is displayed on calendar & on seperate tab',
              'option of CRUDding reminders in the calendar, also all reminders are displaye in seperate tab',
              'option of making the reminders annual, also for hebrew dates! (including hebrew leap year), useful for birthdays and yahrtzeits'
            ],
            'Yes. Landscape only',
              'calendar',
            ]
        ]
      }
    },
  methods: {
    createProjectObj: function(name,link,description,techs,additionals,responsive,img){
      class Proj{
        constructor(proj = {}){
          this.name = name,
          this.link = link,
          this.description = description,
          this.techs = techs,
          this.additionals = additionals,
          this.responsive = responsive ,
          this.img = img
        }
      }
      return new Proj()
    },
      
        getImage: function(picName){
            let img = require.context('../../public/img/', false, /.png$/)
            return img('./' + picName + `.png`)
        },
        displayPic: function(picName){
          this.chosenImage = this.getImage(picName)
        },
        toggleGreenBg(key){
          console.log('key:',key)
          let projects = [...document.querySelectorAll('.project')]
          projects.forEach(project => {
            if (project.classList.contains(key)) project.classList.toggle('greenish')
            else project.classList.remove('greenish')
          })
          let allNotChosen = projects.every(project => !project.classList.contains('greenish'))
          console.log(allNotChosen)
          if (allNotChosen) {
            this.chosenImage = this.getImage("no-signal")
            this.displayPic(this.chosenImage)
          }
        }
  },
  computed: {
    projectObjects: function(){
      return this.projects.map(project => this.createProjectObj(...project))
    }


  }
}
</script>

