<template>
<div class="proj-container " >
    <h1>{{name}}</h1>
    <div>
      description: {{description}}.
    </div>
    <div class="tech-list"> 
        technologies used:
      <ul  v-for="(tech,i) in techs" :key="i+'tech'">
          <li class="tech-item">{{tech}}</li>
      </ul>
    </div>
        <div class="addition-list">
        additional info: 
      <ul  v-for="(addition,i) in additionals" :key="i+'add'">
          <li class="addition-item"><i class="fas fa-snowflake"></i> {{addition}}.</li>
      </ul>
        </div>
      Responsiveness: {{responsive}}
      <div class="app-link"><a :href="link">Go to app <i class="fas fa-directions"></i></a> </div>
      
</div>  
</template>

<script>
export default {
    name: 'Project',
    props:{
        name: String,
        link: String,
        description: String,
        techs: Array,
        additionals: Array,
        responsive: String,
        img: String,
        getImage: Function
    }
}

    

</script>

<style  >

img {
    height: 100px;

}

    .tech-list {
        margin: 5px 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
    
    }
    .tech-item {
        line-height: 20px;
        padding: 6px; 
        margin: 0 2px;
        border-radius: 10px;
        border: 1px solid black;
        background-color: #030;
        font-style: italic;
        /* color: rgb(157, 233, 35); */
        box-shadow: 2px 2px ;
    }
    .addition-item {
        padding-left: 10px;
        margin: 5px 0;
    }
    .fas {
        font-size: 0.7em;
    }
    .app-link a {
        cursor: pointer;
        float: right;
        color: #ede;
        border-bottom: 1px solid transparent;
        margin-right: 10px;
        padding: 3px 6px;
        border-radius: 5px;
        text-decoration: none;
    }

    .app-link a:hover {
        transform: scale(1.2);
        border-bottom: 1px solid #ede;
        transition: 1s;
    }



    .proj-container   {
    position: relative;
    cursor: pointer;
    background-color: rgba(230, 57, 109, 0.1);   
