<script>
import { debounce }  from 'lodash';
export default {
    name: 'About',
    mounted: function(){
        this.debounceFadeText =  debounce(this.fadeText, 100)
          document.addEventListener('scroll', this.debounceFadeText)
        },
    methods: {
        fadeText: function(){
            let aboutTextElements = document.querySelectorAll('.about-text')
            aboutTextElements.forEach(elem => {
                let rect = elem.getBoundingClientRect()
                let top = rect.top + 100
                let bottom = rect.bottom - 250
                let innerHeight = window.innerHeight
                if (innerHeight > top && bottom >= 0) elem.classList.add('fade-in')
                else elem.classList.remove('fade-in')
            })
            }
    }
    
}
</script>

<style scoped>
